import React, { ReactNode } from "react";
import { ButtonContainer, Content } from "./Button.styles";

type Props = {
  children: ReactNode;
  primary: boolean;
  onClickFunc?: () => void;
};

const Button = ({ children, primary, onClickFunc }: Props) => {
  return (
    <ButtonContainer $primary={primary} onClick={onClickFunc}>
      <Content>{children}</Content>
    </ButtonContainer>
  );
};

export default Button;
